

import I18n from '@/i18n';

const OS_TYPE_LINUX = 1;
const OS_TYPE_WINDOWS = 2;
const OS_TYPE_DB = 3;

export default class AccountRule {
    constructor (payload) {
        this.osType = payload.osType;
        this.expression = payload.expression;
        this.description = payload.description;
    }

    /**
     * @desc 
     * @returns { String }
     */
    get osTypeKey () {
        const textMap = {
            [OS_TYPE_LINUX]: 'linux',
            [OS_TYPE_WINDOWS]: 'windows',
            [OS_TYPE_DB]: 'db',
        };
        return textMap[this.osType];
    }

    /**
     * @desc 
     * @returns { String }
     */
    get osTypeText () {
        const textMap = {
            [OS_TYPE_LINUX]: I18n.t('Linux '),
            [OS_TYPE_WINDOWS]: I18n.t('Windows '),
            [OS_TYPE_DB]: I18n.t(''),
        };
        return textMap[this.osType];
    }
}
